////
/// Bayes campaign banner
////

// some override rules

.banner--bayes {
    background-color: $color-purewhite;
    color: $color-nearblack;
}

.wrapper--txt-ribbon {
    @include media('>tablet') {
        grid-column: 12/-2;
        grid-row: 1/-1;
    }
}

.banner__img {
    @include media('>tablet') {
        grid-column: 1 / 12;
    }
}

.banner__ribbon {
    position: relative;
}

// new elements

.banner__separator {
    @include media('>tablet') {
        grid-column: 12/13;
        grid-row: 1/-1;
        border-top: 3rem solid $color-purewhite;
        border-right: 3rem solid $color-purewhite;
        border-bottom: 3rem solid $color-purewhite;
    }
}

.banner__ribbon__text {
    @include h2style();
}

.banner__bracket--left,
.banner__bracket--right {
    color: $color-corporate-accent-2;
    @include h1style();
    position: absolute;
    font-weight: $font-weight-black;
}

.banner__bracket--left {
    top: - $measure--basic;
    left: - $measure--half;
}
